import React from 'react';

import styles from 'styles/post.module.scss';
import confused from './confused.gif';

class MutualFunds extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L2 : THE SAVINGS DILEMMA</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>So where do I save<br/>my money?</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <p>On one hand, we have Savings Account which barely generates any returns, and on the other hand, we have Fixed Deposits which give slightly better returns but then you don't have any flexibility.</p>
            <br/>
            <div className={styles["box"]}>
              <p style={{color: "var(--secondary-highlight)"}}><b><i>So how do you really get your money to grow?</i></b></p>
              <p>More importantly, how do you get your money to compound at a high rate so that the money that you make shit tons of money 🤑</p>
            </div>
            <br/>
            <p>One thing you could do is invest in stocks of companies. If you understand how to read company annual reports, analyze a company's performance and understand how well positioned they are for the future, this is definitely the way to go.</p>
            <br/>
            <img src={confused}/>
            <br/><br/>
            <p>But what if you don't have this expertise and the time to build this expertise, and still want the sweet returns of the stock market?</p>
            <br/>
            <p><i><b>Introducing : Mutual Funds</b></i></p>
            <br/>
            <p>So, what is it?</p>
            <br/>
            <p className={styles["highlight"]}>Mutual Fund is a fund made of money that people like you and I invest. Every Mutual Fund has a fund manager who invests this money on our behalf.</p>
            <br/>
            <p>This might be in Stocks or Debt (we'll explain).</p>
            <br/>
            <p>That's it.</p>
            <br/>
            <p>Chances are, if you've watched TV in the past two years, you must have heard the slogan <i style={{fontWeight: "600"}}>"Mutual Fund Sahi Hai"</i>.</p>
            <br/>
            <p>If you've heard that, you must also have heard that <i style={{fontWeight: "600"}}>"Mutual Funds are subject to Market Risks"</i>.</p>
            <br/>
            <p>So in here, we'll help you:</p>
            <p>• Understand what Mutual Funds are and how they work</p>
            <p>• What are the different types of Mutual Funds</p>
            <p>• How to choose the right one(s) for you</p>
          </div>
      </div>
    );
  }
}

export default MutualFunds;