import React from 'react';

import styles from 'styles/post.module.scss';
import baby from './baby.gif';

class FixedDeposit extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L2 : THE SAVINGS DILEMMA</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>Fixed Deposits</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
            <div className={styles["tldr"]}>
              <h2 className={styles["heading"]}>⚡️ Here’s what this lesson is all about :</h2>
              <p>With Fixed Deposits, you can't withdraw the money whenever you want, but you get a higher return.</p>
              <br/>
              <p>Read on to know the trade-offs you're making 👇</p>
            </div>
            <p>Everyone knows about Fixed Deposits.</p>
            <p>If you are a 90s kid, chances are that you already have an FD, and your father made you open it 😛</p>
            <br/>
            <h5>SO, HOW DOES IT WORK?</h5>
            <div className={styles["box"]} style={{fontWeight: "900"}}>
              <p>Simple. You deposit money for a fixed duration and you get higher returns than a savings account.</p>
            </div>
            <br/>
            <p>You can start an FD from 7 days to upto 10 years (💡 FYI, this duration is called the Tenure or Maturity Period of the FD).</p>
            <br/>
            <p>Fixed Deposits are very safe because they give guaranteed returns. They also have an insurance upto ₹5 Lakhs. Sweeet! 🤓</p>
            <br/>
            <p>So what’s the catch here?</p>
            <p style={{color: "#FF7979"}}>Withdrawing Fixed Deposits before the term is over can lead to penalty. You could be asked to pay a penalty of 0.05%-1%</p>
            <br/>
            <p className={styles["highlight"]}>
              So in an FD, you can't withdraw the money whenever you want, but you get a higher return.
              <br/>
              In a Savings Account, you can withdraw the money whenever you want, but you get a lower return.
            </p>
            <br/>
            <p>Hmm ... What if there was something where you could withdraw money whenever you wanted and get higher returns than a Savings Account 🤔🤔🤔</p>
            <br/>
            <p>Interested? Hop on to the next chapter to read on 👇</p>
            <br/>
            <img src={baby}/>
          </div>
      </div>
    );
  }
}

export default FixedDeposit;