import React from 'react';

import styles from 'styles/post.module.scss';
import crabs from './crabs.gif';

class SavingsAccount extends React.Component {

  render(){
    return (
      <div className={styles["post"]}>
          <div className={styles["header"]}>
            <h3 className={styles["sub-title"]}>L2 : THE SAVINGS DILEMMA</h3>
            <div className={styles["title-box"]}>
              <div className={styles["container"]}>
                <h3 className={styles["title"]}>The Savings Account</h3>
                <div className={styles["title-line"]}></div>
              </div>
            </div>
          </div>
          <div className={styles["post-content"]}>
          <div className={styles["tldr"]}>
            <h2 className={styles["heading"]}>⚡️ Here’s what this lesson is all about :</h2>
            <p>Savings Accounts are really flexible, but they destroy your money over time because the interest they offer is less than inflation.</p>
            <br/>
            <p>Read on to know the good and the bad about your bank account 👇</p>
          </div>
          <p>First up is the good ol' Savings Account.</p>
          <p>Most of us save a lot of money in multiple Savings Account.</p>
          <br/>
          <p>So here’s the scoop 🙌 :</p>
          <p>Savings Accounts are definitely good ... but they are not the best.</p>
          <p>Let us explain : </p>
          <br/>
          <h5>HERE’S THE GOOD</h5>
          <p>The best thing about savings accounts is that you can withdraw your money whenever you want (💡FYI, that’s called liquidity)</p>
          <br/>
          <p>Another great thing about Savings Account is that the <span style={{fontWeight: "600"}}><i>Government gives you a ₹5 Lakh insurance on your deposit</i></span>.</p>
          <p>So even if your Bank shuts down tomorrow, you will get your all money back (it’s only upto ₹5 Lakhs, so if you had ₹8 Lakhs in your account, you’d only get 5 back)</p>
          <br/>
          <h5>HERE'S WHY IT SUCKS</h5>
          <p>The bad thing about savings accounts is that most Savings accounts offer measly 2.7-4.5% returns on on the money deposited in the account.</p>
          <br/>
          <p>Like we explained earlier, it does not even cover inflation, so the value of your money reduces over time.</p>
          <br/>
          <img src={crabs}/>
          <br/><br/>
          <p>So savings accounts make sure that your money is safe, but the returns are just disappointing 😑</p>
          <p>So where do you put your money, if you want the security of a bank account and better returns?</p>
          <p>Meet : Fixed Deposit 👇</p>
          </div>
      </div>
    );
  }
}

export default SavingsAccount;