import {Switch, Route} from 'react-router-dom';
import React from 'react';

import SavingsAccount from 'pages/savings/savings-account';
import FixedDeposit from './fixed-deposit';
import MutualFunds from './mutual-funds';

import { Quiz } from 'components/quiz';

var saQuiz = [
  {
      "question": "Savings account usually give annual returns of ___",
      "options": [
        "1-2 %",
        "2.7-4%",
        "6-7%",
        ">10%"
      ],
      "answer": 1
  },
  {
      "question": "How much is your money insured for in a Savings account?",
      "options": [
        "2L",
        "3L",
        "4L",
        "5L"
      ],
      "answer": 3
  }
]

var fdQuiz = [
  {
      "question": "What's the lowest tenure possible in an FD?",
      "options": [
        "7 days",
        "1 month",
        "6 months",
        "1 year"
      ],
      "answer": 0
  },
  {
      "question": "There is no penalty for withdrawing from an FD before the tenure ends",
      "options": [
        "True",
        "False",
      ],
      "answer": 1
  },
  {
      "question": "How much is your money insured for in an FD?",
      "options": [
        "2L",
        "3L",
        "4L",
        "5L",
      ],
      "answer": 3
  }
]

class Savings extends React.Component {

  render(){
    return (
        <Switch>
            <Route path="/post/the-savings-dilemma/savings-account/quiz"><Quiz questions={saQuiz}/></Route>
            <Route path="/post/the-savings-dilemma/savings-account"><SavingsAccount/></Route>
            <Route path="/post/the-savings-dilemma/fixed-deposits/quiz"><Quiz questions={fdQuiz}/></Route>
            <Route path="/post/the-savings-dilemma/fixed-deposits"><FixedDeposit/></Route>
            <Route path="/post/the-savings-dilemma/so-where-do-i-save-my-money"><MutualFunds/></Route>
            <Route path="/post/the-savings-dilemma"><SavingsAccount/></Route>
        </Switch>
    );
  }
}

export default Savings;